import {Pipe} from "@angular/core";
import {PipeTransform} from "@angular/core";
@Pipe({
name:"mywordPipe"
}
)
export class NumToWordPipe implements PipeTransform
{
    transform(value:string):string
    {
        switch(value){
            case "1" :
                return "One";
             case "2" :
                return "Two";
             case "3" :
                return "Three";
              default:
                 return "Not a Number";   
        }
    }
}
