import {Injectable} from "@angular/core";
@Injectable()
export class EmployeeService
{
    data=[{id:1,name:"Abc"},
          {id:2,name:"Xyz"},
          {id:2,name:"Ram"}];
    getAll():any[]
    {
        return this.data;
    }       
    getEmployeeById(Id:number):any
    {
        var Obj={};
        for(let item of this.data)
        {
            if(item.id==Id)
            {
                Obj=item;
                break;
            }
        }
        return Obj;
    }
}