import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { InterpolationComponent } from './Interpolation/interpolation.component';
import { Directive1Component } from './Directive1/directive1.component';
import { Directive2Component } from './Directive2/directive2.component';
import { TechComponent } from './InputOutput/tech.component';
import { TechDetailsComponent } from './InputOutput/techdetails.component';
import { BillComponent } from './InbuiltPipes/bill.component';
import { NumToWordComponent } from './Pipes/NumtoWord.component';
import { EmployeeListComponent } from './Employees/employeelist.component';


const routes: Routes = [{path:"interpolation",component:InterpolationComponent},
                         {path:"directive1",component:Directive1Component},
                         {path:"directive2",component:Directive2Component},
                         {path:"input",component:TechComponent},
                         {path:"output",component:TechDetailsComponent},
                         {path:"pipe",component:BillComponent},
                         {path:"Cpipe",component:NumToWordComponent},
                          {path:"pipe",component:BillComponent},
                          {path:"service",component:EmployeeListComponent}

                       ];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
