import {Component} from "@angular/core";
@Component({
    selector:"my-student_Details",
    templateUrl:"./student_details.component.html"
})
export class StudentDetailsComponent{
    proll:string;
    data=[{roll:"1",name:"Abc"},
    {roll:"2",name:"Pqr"},
    {roll:"3",name:"Xyz"}
];
onDataReceive(val:string)
{
    this.proll=val;
}
}