import {Pipe} from "@angular/core";
import {PipeTransform} from "@angular/core";
@Pipe({
    name:"myAreaCodePipe"
})
export class AreaCodePipe implements PipeTransform
{
    transform(code:string):string
    {
        switch(code)
        {
            case "12" :
                return "Pune";
             case "13" :
                return "SolaPur";
             case "14" :
                return "Sayali";
              default:
                 return "Not Found";   
        }
    }
}